* {
  font-family: Arial, Helvetica, sans-serif;
  font-size: small
}

body{
  padding: 60px;
}

input{
  font-size: 1.2em;
  width: 100%;
  color: grey;
}

.card {
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  overflow: hidden;
  margin-top: 10px;
  margin-right: 10px;
}


.card p {
  padding: 5px;
  text-align: center;
}

.card img {
  max-height: 170px;
}

.cardGrid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
